.button {
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 500;
    background-color: #fff;
    color: #303030;
    border: 1px solid #ccc;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    position: relative;
}

.button:hover {
    border-color: lightskyblue;
    color: lightskyblue
}

.button.primary {
    background-color: lightskyblue;
    color: #fff;
    border: 0;
}

.dialog {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: none;
    opacity: 0;
    transition: all .3s;
}

.dialog.show {
    display: block;
}

.dialog.appear {
    opacity: 1;
}

.dialog .main {
    width: 60%;
    background-color: #fff;
    margin: 30px auto;
    padding: 16px;
    border-radius: 6px;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.3);
    transform: translateY(-100%);
    opacity: 0;
    transition: all .3s;
}

.dialog.appear .main {
    transform: translateY(40px);
    opacity: 1;
}


.dialog .header {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
}

.dialog .header .close {
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    padding: 4px;
    cursor: pointer;
}

.dialog .header .close::before,
.dialog .header .close::after {
    content: '';
    position: absolute;
    display: block;
    width: 10px;
    height: 1px;
    background: #333;
    transform: rotate(45deg);
}

.dialog .header .close::after {
    transform: rotate(-45deg);
}

.dialog .footer {
    display: flex;
    justify-content: flex-end;
}

.dialog .footer .button {
    margin-left: 10px;
}